body{
    padding: 0;
    margin: 0;
    background-color: #fff ;
    transition: all 0.3s ease;
    

    
}
html{
    scroll-behavior: smooth;
}
.dark-mode {
      background-color: #121212dc;
      color: white;


 }
 .dark-mode .navbar{
     background-color: #121212a2 !important;
   
     z-index: 88888888 !important;
 }
  .dark-mode .homeImg{
   
    margin-top: 65px !important;
    border-radius: 40px 0px 40px 0px !important;
  }
  .dark-mode .homeImg{
    width:78% !important;
  }
  .dark-mode option{
    color: #f17732 !important;
  } 
 .dark-mode .product-card{
    box-shadow: 10px 10px 5px 5px #fff !important;
 }
 .dark-mode ul li ,.dark-mode .abouticon {
    color: #fff !important;
 }
 .dark-mode .cartBody *{
    background-color:  #121212dc !important ;
    
 }
 .dark-mode .btn-submit{
    background-color: #f17732 !important;
 }
:root{
    --main-color:#006c52;
    --orange-color:#f17732;
    --btn-color:#565acf;
}
section ,footer ,header{
    width: 100%;
    max-width: 100%;
    height: auto;
    margin-top: 50px;
    overflow: hidden;
    padding: 25px 0;

}

a{
    text-decoration: none;
}