.aboutUs{
    background-color: rgba(184, 184, 190, 0.195);
    border-radius: 40px;
    margin: 10px;
}
.aboutUs *{
    background-color: transparent;
}
.deltaImg{
    width: 100%;
    border-radius: 25px;
    height: 400px;
}
.aboutUs h2 ,.aboutUs h3{
    color: #006c52 !important;
    font-size: 30px;
    font-weight: 600;
    text-transform: capitalize;

}
.aboutUs a{
    text-decoration: none;
}
.aboutUs p{
    opacity: 0.7;
    font-size: 18px;
}
