.searchinput{
     padding: 12px 12px;
    border-radius: 20px;
    
    outline: none;
    width: 50%;
    margin: auto ;
   
}
.searchinput:hover{
     /* border: 2px solid #b5b5b841 ; */
     box-shadow: 10px 10px 5px #b5b5b841;
}
.product-card{
     box-shadow: 10px 10px 5px #b5b5b8bb;
}
.productContent h4{
    color:#429a2b !important;
    font-size: 30px;
    padding-bottom: 20px;
   letter-spacing: 1px;

}
.productContent h5{
    color:#f17732;
    padding-bottom: 20px;
    opacity: 0.9;

}
.imgMenu{
    width: 200px !important;
    height: 210px !important;
    border-radius: 10px;
    margin: 20px;

}
.productContent h6{
    color: #388026;
    font-size: 25px;
}

.productContent:hover ,.imgMenu:hover {
    transform: translateY(-6px) !important;
    transition: 0.4s ease-in-out !important;
   }